const PubFuc = {
	// 10000w 
	peoNum:(count)=>{
		let peoCount = Number(count)
		if(peoCount){
			if(peoCount/10000>1){
				return (peoCount/10000).toFixed(1)+''
			}else{
				return peoCount
			}
		}else{
			return 0
		}
	},
	// 00:00
	secondFormact:(second)=>{
		var sec = second%60;
		var min = Math.floor(second/60);
		if(min.toString().length<2){
			min = '0'+min;
		}
		if(sec.toString().length<2){
			sec = '0'+sec;
		}
		return min+':'+sec
	}
}
export default PubFuc